body {
    font-size: 1.5vw;
    color: #191F18;
    overflow-x: hidden;
    background-color: #FCFAFE;
    transition-property: background-color;
    transition-duration: 0.9s;
    transition-delay: 0.5s;
}

.bg-dark {
    background-color: #191F18;
}
.bg-white {
    background-color: #FCFAFE;
}
.white {
    color: #FCFAFE !important;
}
.black {
    color: #191F18 !important;
}


h1,h2,h3,h4,.big{
    font-family: 'Italiana', serif;
    font-weight: normal;
}

h1,.big {
    font-size: 6em;
    line-height: 1em;
}

h2 {
    font-size: 5.5em;
    line-height: 1em;
}
h3 {
    font-size: 4.0em;
    line-height: 1em;
}

a,p,small,strong,button, li {
    font-family: 'DM Sans', sans-serif;
}

span {
    font-family: inherit;
}

li{
    list-style: none;
    margin: 0;
}

button {
    font-weight: bold;
    background-color: transparent;
    border-color: #60170C;
    color: #60170C;
    border-style: solid;
    padding-top: 5em;
    padding-bottom: 5em;
    padding-left: 1.1em;
    padding-right: 1.1em;
    border-radius: 50%;
    letter-spacing: 0.3em;
    position: relative;
    overflow: hidden;
}


button .dot {
    height: 5em;
    width: 5em;
    background-color: #d67f72;
    position: absolute;
    bottom: 1em;
    left: 30%;
    top: 30%;
    border-radius: 50%;
    z-index: -1;
}

.cta-cursor {
    font-weight: bold;
    background-color: #FCFAFE;
    color: #191F18;
    padding-top: 2em;
    padding-bottom: 2em;
    padding-left: 2em;
    padding-right: 2em;
    border-radius: 50%;
    letter-spacing: 0.3em;
    mix-blend-mode: difference;
}
.accent {
    font-weight: bold;
    letter-spacing: 0.3em;
}

.columns-1 {
    width: 16.66666666666667%;
}

.columns-2 {
    width: 33.33333333333333%;
}

.columns-3 {
    width: 50%;
}

.columns-4 {
    width: 66.66666666666667%;
}

.columns-5 {
    width: 83.33333333333333%;
}

.columns-6 {
    width: 100%;
}

.arrow {
    cursor: pointer;
    display: inline-block;
    transition-property: margin;
    transition-duration: 0.5s;
}

.arrow:hover {
    margin-bottom: .1em;
}

@media only screen and (max-width: 385px) {
    body {
        font-size: 2.2vw;
    }

    .columns-1 {
        width: 100%;
    }

    .columns-2 {
        width: 50%;
    }

    .columns-4 {
        width: 100%;
    }

    .columns-5 {
        width: 100%;
    }

    .columns-6 {
        width: 100%0;
    }

    .offset {
        display: none;
    }


    p,a,strong {
        font-size: 1.5em;
        text-align: left !important;
    }

}